body {
    margin: 0;
    padding: 0;
    background-color: #999999;
    height: 100%;
}
html {
    height: 100%;
}

header,
footer {
    z-index: 2;
}

.wrapper {
    position: absolute;
    margin: auto;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    width: fit-content;
    text-align: center;
    z-index: 4;
}
.wrapper span {
    position: relative;
    color: #fff;
    font-weight: 900;
    font-size: 20.4em;
    display: block;
    overflow: hidden;
    width: fit-content;
    height: max-content;
}
.wrapper span:first-letter {
    letter-spacing: 12vmax;
}
.wrapper span:before {
    content: "";
    background-image: url("https://staticdelivery.nexusmods.com/mods/1151/images/528-0-1447526230.png");
    position: absolute;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    animation: rotateIn 0.5s ease-out;
}
.wrapper p {
    text-align: center;
    font-style: italic;
    font-weight: 400;
    color: #fff;
    margin-top: 0;
    line-height: 22px;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(-360deg);
    }
}
@keyframes rotateIn {
    from {
        transform: rotate(0deg) scale(0.2);
        opacity: 0;
    }
    to {
        transform: rotate(360deg) scale(1);
        opacity: 1;
    }
}
.space {
    position: absolute;
    width: 400vw;
    height: 400vh;
    top: 50%;
    left: 50%;
    z-index: 1;
    margin-top: -200vh;
    margin-left: -200vw;
    animation: spin 240s linear infinite;
    background-size: 240px;
    backface-visibility: visible;
    background-image: url(data:image/svg+xml;base64,);
}
